body {
  margin: 0;
  font-family: Arial, sans-serif;
  background: #f4f7fa;
  color: #333;
}

.App {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

header {
  text-align: center;
  margin-bottom: 40px;
}

header h1 {
  color: #007acc;
}

section {
  margin-bottom: 30px;
  padding: 20px;
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}

ul {
  padding-left: 20px;
}

a {
  color: #007acc;
}

footer {
  text-align: center;
  margin-top: 40px;
  font-size: 14px;
  color: #777;
}